.redtag-clients {
    background-color: white;
    padding: 10rem 0 12.5rem;

    @include breakpoint(large-desktop) {
        padding: 10rem 0 10rem;
    }
    @include breakpoint(mobile) {
        padding: 15rem 0 5rem;
    }
    position: relative;
    &__content {
    }

    &__title {
        color: #010101;
        font-family: 'DIN Pro', sans-serif;
        font-size: 5.4rem;
        font-style: normal;
        font-weight: 700;
        line-height: 110%; /* 59.4px */

        width: 100%;
        max-width: 120rem;
        margin-bottom: 4rem;

        @include breakpoint(mobile) {
            font-size: 3.2rem;
            width: 36rem;
        }
        @include breakpoint(supersmall-mobile) {
            width: 100%;
        }
    }

    &__info {
        display: flex;
        align-items: flex-start;
        column-gap: 2rem;

        @include breakpoint(small-tablet) {
            row-gap: 2rem;
            display: grid;
            grid-template-columns: repeat(2, minmax(0, 0.35fr));
        }

        @include breakpoint(mobile) {
            display: flex;
            flex-direction: column;
        }
    }

    &__list {
        width: 100%;
        max-width: 28.5rem;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
    }

    &__list:nth-child(odd) > &__number {
        color: #c20a26;
        font-family: 'Mont', sans-serif;
        font-size: 9.8rem;
        font-style: normal;
        font-weight: 900;
        line-height: 90%;
    }

    &__list:nth-child(even) > &__number {
        color: white;
        font-family: 'Mont', sans-serif;
        font-size: 9.8rem;
        font-style: normal;
        font-weight: 900;
        line-height: 90%;

        -webkit-text-fill-color: white;
        -webkit-text-stroke-color: #c20a26;
        -webkit-text-stroke-width: 0.2rem;
    }

    &__text {
        color: #010101;
        font-family: 'DIN Pro', sans-serif;
        font-size: 1.6rem;
        font-style: normal;
        font-weight: 700;
        line-height: 130%;
        width: 100%;
        max-width: 25rem;
    }

    &__line {
        display: flex;
        width: 100%;
        column-gap: 3rem;
        position: relative;
        transform: rotate(-2.5deg);
        margin-top: 10rem;

        @include breakpoint(small-tablet) {
            margin-top: 7.5rem;
        }
    }

    img {
        width: 100%;
        height: auto;
        object-fit: cover;

        @include breakpoint(small-tablet) {
            width: 50%;
        }
    }

    &__polygon {
        position: absolute;
        width: 100%;
        height: 9.6vw;
        bottom: 0;

        clip-path: polygon(100% 50%, 0% 100%, 100% 100%);
        background: #ff173b;
        background: -webkit-gradient(linear, left top, right top, from(#ff173b), to(#bb021e));
        background: linear-gradient(90deg, #ff173b 0%, #bb021e 100%);
    }
}
